// Вызов миксина с передачей аргументов. Передаем разные
// начертания (файлы) для разных `font-weight`.
@import './_mixins/_font-face';

@include font-face('Museo', '../fonts/light', $weight: 300);
@include font-face('Museo', '../fonts/Regular');
@include font-face('Museo', '../fonts/Medium', $weight: 600);
@include font-face('Museo', '../fonts/Bold', $weight: 800);
@include font-face('Museo', '../fonts/Black', $weight: 900);

// Назначение главного шрифта
body {
	font-family: 'Museo', sans-serif; }
